.single-movie {
  .hero {
    position: relative;
    height: 89vh;
    z-index: 1;
    background-position: 340px -360px;
    background-size: cover;
    background-repeat: repeat;
    color: white;
    .thumnail {
      position: absolute;
      background: linear-gradient(
        to bottom right,
        rgba(31.5, 10.5, 10.5, 1),
        rgba(31.5, 10.5, 10.5, 0.84)
      );
      height: 100%;
      width: 100%;
      z-index: -1;
      top: 0;
      left: 0;
    }
    .flex {
      z-index: 1;
      display: flex;
      justify-content: space-between;
      align-items: center;
      width: 95%;
      margin: auto;
      padding: 3rem 0;
      .img {
        width: 24%;
        img {
          height: 100%;
        }
      }
      .info {
        width: 73%;
        display: flex;
        flex-direction: column;
        gap: 7px;
        .name {
          h1 {
            font-size: 35px;
          }
          span {
            font-weight: 400;
          }
        }
        .movie-genre {
          span:nth-child(2) {
          }
          .genre {
            display: inline-block;
            span {
              padding: 0 10px;
            }
          }
        }
        .tagline {
          font-style: italic;
          font-weight: 400;
          color: #545454;
        }
        .overview {
          h4 {
            font-weight: 700;
            text-transform: capitalize;
            margin: 20px 0 10px;
          }
          p {
            font-weight: 500;
            font-size: 16px;
            line-height: 20px;
          }
        }
        .creator {
          display: flex;
          align-items: center;
          justify-content: space-between;
          width: 60%;
          margin: 15px 0;
          p {
            margin: 10px 0;
          }
        }
      }
    }
  }
}
@media (max-width: 600px) {
  .single-movie {
    .hero {
      position: relative;
      height: 100%;
      z-index: 1;
      background-position: 340px -360px;
      background-size: cover;
      background-repeat: repeat;
      color: white;
      .thumnail {
        position: absolute;
        background: linear-gradient(
          to bottom right,
          rgba(31.5, 10.5, 10.5, 1),
          rgba(31.5, 10.5, 10.5, 0.84)
        );
        height: 100%;
        width: 100%;
        z-index: -1;
        top: 0;
        left: 0;
      }
      .flex {
        z-index: 1;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        align-items: center;
        width: 90%;
        margin: auto;
        padding: 3rem 0;
        .img {
          width: 90%;
          margin: 10px auto;
          img {
            height: 100%;
          }
        }
        .info {
          width: 100%;
          display: flex;
          flex-direction: column;
          gap: 7px;
          .name {
            h1 {
              font-size: 30px;
            }
            span {
              font-weight: 400;
            }
          }
          .movie-genre {
            margin: 10px 0;
            .genre {
              display: block;
              margin: 10px 0;
              span {
                padding: 0 10px 0 0;
              }
            }
          }
          .rate {
            .score {
              p {
                margin: 20px 0;
                span {
                  padding: 15px 10px;
                }
              }
            }
          }
          .tagline {
            font-style: italic;
            font-weight: 400;
            color: #545454;
          }
          .overview {
            h4 {
              font-weight: 700;
              text-transform: capitalize;
              margin: 20px 0 10px;
            }
            p {
              font-weight: 500;
              font-size: 16px;
              line-height: 20px;
            }
          }
          .creator {
            display: flex;
            align-items: center;
            justify-content: space-between;
            width: 60%;
            margin: 15px 0;
            p {
              margin: 10px 0;
            }
          }
        }
      }
    }
  }
}
